<template>
  <div class="process-state-count-model">
    <div v-show="!msg">
      <div class="data-area" v-spin="isSpin" style="height: 430px;">
        <div class="col-md-7">
          <div id="process-state-pie" style="height:260px;margin-top: 100px;"></div>
        </div>
        <div class="col-md-5">
          <div class="table-small-model">
            <table>
              <tr>
                <th width="40">{{$t('#')}}</th>
                <th>{{$t('Number')}}</th>
                <th>{{$t('State')}}</th>
              </tr>
              <tr v-for="(item,$index) in processStateList">
                <td><span>{{$index+1}}</span></td>
                <td><span><a href="javascript:" @click="_goProcess(item.key)"
                             :class="'links'">{{item.value}}</a></span></td>
                <td><span class="ellipsis" style="width: 98%;" :title="item.key">{{item.key}}</span></td>
              </tr>
            </table>
          </div>
        </div>
      </div>
    </div>
    <div v-show="msg">
      <m-no-data :msg="msg" v-if="msg" :height="430"></m-no-data>
    </div>
    <div v-if="dialogVisible" id="modle" @click="closeDialog">
      <!--<el-dialog :visible.sync="dialogVisible" width="80%" :append-to-body="true" @close="closeDialog">-->
      <mInstanceList :params="params" style="width: 1224px;margin-left: 250px;margin-top: 80px;margin-bottom: 100px;"></mInstanceList>
      <!--</el-dialog>-->
    </div>
  </div>
</template>
<script>
  import _ from 'lodash'
  import {mapActions} from 'vuex'
  import {pie} from './chartConfig'
  import Chart from '~/@analysys/ana-charts'
  import mNoData from '@/module/components/noData/noData'
  import {stateType} from '@/conf/home/pages/projects/pages/_source/instanceConditions/common'
  import mInstanceList from "@/conf/home/pages/projects/pages/instance/pages/list/index";

  export default {
    name: 'process-state-count',
    data() {
      return {
        isSpin: true,
        msg: '',
        processStateList: [],
        dialogVisible: false,
        params: { stateType: "", startDate: "", endDate: "" }
      }
    },
    props: {
      searchParams: Object
    },
    methods: {
      ...mapActions('projects', ['getProcessStateCount']),
      _goProcess(name) {
        // this.$router.push({
        //   name: 'projects-instance-list',
        //   query: {
        //     stateType: _.find(stateType, ['label', name])['code'],
        //     startDate: this.searchParams.startDate,
        //     endDate: this.searchParams.endDate
        //   }
        // })
        this.params.stateType = _.find(stateType, ["label", name])["code"];
        this.params.startDate = this.searchParams.startDate;
        this.params.endDate = this.searchParams.endDate;
        this.dialogVisible = true;

      },
      closeDialog() {
        this.dialogVisible = false;
      },
      _handleProcessState(res) {
        let data = res.data.taskCountDtos
        this.processStateList = _.map(data, v => {
          return {
            key: _.find(stateType, ['code', v.taskStateType])['label'],
            value: v.count
          }
        })
        const myChart = Chart.pie('#process-state-pie', this.processStateList, {title: ''})
        myChart.echart.setOption(pie)
        // 首页不允许跳转
        // if (this.searchParams.projectId) {
          myChart.echart.on('click', e => {
            this._goProcess(e.data.name)
          })
        // }
      }
    },
    watch: {
      'searchParams': {
        deep: true,
        immediate: true,
        handler(o) {
          this.isSpin = true
          this.getProcessStateCount(o).then(res => {
            this.processStateList = []
            this._handleProcessState(res)
            this.isSpin = false
          }).catch(e => {
            this.msg = e.msg || 'error'
            this.isSpin = false
          })
        }
      }
    },
    beforeCreate() {
    },
    created() {
    },
    beforeMount() {
    },
    mounted() {
    },
    beforeUpdate() {
    },
    updated() {
    },
    beforeDestroy() {
    },
    destroyed() {
    },
    computed: {},
    components: {mNoData,mInstanceList}
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  .process-state-count-model {

  }
  #modle {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    outline: 0;
    -webkit-overflow-scrolling: touch;
    background-color: rgb(0, 0, 0);
    filter: alpha(opacity=60);
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 9999;
  }
</style>



